'use client';
import { motion } from 'framer-motion';
import React from 'react';

const Card = ({ title, description, children }: any) => (
  <motion.div
    initial={{ opacity: 0, y: 40 }}
    whileInView={{ opacity: 1, y: 0 }}
    transition={{ duration: 0.6 }}
    className="group relative w-full md:w-[48%] bg-gradient-to-b from-[#0B1627] to-[#0D1B2A] p-6 rounded-3xl border border-[#223347] shadow-xl overflow-hidden"
  >
    <div className="absolute inset-0 bg-gradient-to-r from-cyan-400/10 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-700 pointer-events-none" />
    <div className="absolute top-0 left-0 w-full h-full rounded-3xl border border-transparent group-hover:border-cyan-400 transition-all duration-700 animate-border-glow"></div>
    <h3 className="text-white text-xl font-bold mb-2 z-10 relative">{title}</h3>
    <p className="text-sm text-gray-300 mb-4 z-10 relative">{description}</p>
    {children}
  </motion.div>
);

export default function WhatWeDoBest() {
  return (
    <div className="w-full min-h-screen px-6 py-20 bg-gradient-to-tr from-[#0D1B2A] via-[#091623] to-[#050B14] text-white font-sans">
      <div className="text-center mb-14">
        <h2 className="text-4xl font-extrabold text-white">What We Do Best</h2>
        <p className="text-gray-400 mt-3">From Strategy to Spotlight â€” We Do it All.</p>
      </div>
      <div className="flex flex-col md:flex-row justify-between gap-8">
        <Card
          title="Digital Marketing"
          description="From SEO and PPC to email and content, we tailor strategies that drive results."
        >
          <div className="flex flex-wrap gap-3 mt-3 z-10 relative">
            <span className="bg-[#1D2A3A] px-3 py-1 rounded-full text-sm border border-[#29455F] hover:ring-2 hover:ring-cyan-300 transition">SEO</span>
            <span className="bg-[#1D2A3A] px-3 py-1 rounded-full text-sm border border-[#29455F] hover:ring-2 hover:ring-cyan-300 transition">Email Marketing</span>
            <span className="bg-[#1D2A3A] px-3 py-1 rounded-full text-sm border border-[#29455F] hover:ring-2 hover:ring-cyan-300 transition">Paid Advertising</span>
            <span className="bg-[#1D2A3A] px-3 py-1 rounded-full text-sm border border-[#29455F] hover:ring-2 hover:ring-cyan-300 transition">Google Ads</span>
          </div>
          <motion.img
            src="/bar-growth.svg"
            alt="Growth Chart"
            className="w-full max-w-xs mx-auto mt-8"
            animate={{ scale: [1, 1.05, 1] }}
            transition={{ repeat: Infinity, duration: 4 }}
          />
        </Card>

        <Card
          title="Social Media Management"
          description="More than matchmaking! We script, plan, and scale creator partnerships with real impact."
        >
          <motion.div
            animate={{ rotate: 360 }}
            transition={{ repeat: Infinity, duration: 30, ease: 'linear' }}
            className="relative w-48 h-48 mx-auto mt-6"
          >
            <div className="absolute top-0 left-1/2 -translate-x-1/2">
              <img src="/icons/facebook.svg" className="w-6" />
            </div>
            <div className="absolute right-0 top-1/2 -translate-y-1/2">
              <img src="/icons/tiktok.svg" className="w-6" />
            </div>
            <div className="absolute bottom-0 left-1/2 -translate-x-1/2">
              <img src="/icons/youtube.svg" className="w-6" />
            </div>
            <div className="absolute left-0 top-1/2 -translate-y-1/2">
              <img src="/icons/instagram.svg" className="w-6" />
            </div>
            <div className="absolute top-1/2 left-1/2 w-16 h-16 bg-[#0A1C2B] rounded-full -translate-x-1/2 -translate-y-1/2 border border-[#223347]" />
          </motion.div>
        </Card>
      </div>
    </div>
  );
}
